import { collection, addDoc, getDocs, getDoc, updateDoc, deleteDoc, doc, serverTimestamp, query, orderBy, writeBatch } from 'firebase/firestore';
import { db } from '../firebase';

const DC_COLLECTION = 'deliveryChallans';
const DC_ACTIVITY_COLLECTION = 'dcActivity';

export function normalizeChallanForSave(raw) {
	const items = Array.isArray(raw.items) ? raw.items : [];
	const safeItems = items.map((it) => {
		const quantity = Number(it.quantity) || 0;
		const price = Number(it.price) || 0;
		return {
			description: String(it.description || '').trim(),
			hsnCode: String(it.hsnCode || ''),
			quantity,
			unit: String(it.unit || 'Nos'),
			price,
			amount: Number((quantity * price).toFixed(2)),
			itemId: it.itemId || null,
		};
	});

	const totalAmount = safeItems.reduce((sum, it) => sum + (Number(it.amount) || 0), 0);
	const totalQuantity = safeItems.reduce((sum, it) => sum + (Number(it.quantity) || 0), 0);

	return {
		challanNumber: String(raw.challanNumber || '').trim(),
		date: raw.date || new Date().toISOString().split('T')[0],
		poId: raw.poId ? String(raw.poId) : null,
		poNumber: raw.poNumber ? String(raw.poNumber) : (raw.reference ? String(raw.reference) : ''),
		vendorId: String(raw.vendorId || '').trim(),
		vendorName: String(raw.vendorName || ''),
		vendorAddress: String(raw.vendorAddress || ''),
		reference: String(raw.reference || ''),
		vehicleNo: String(raw.vehicleNo || ''),
		driverName: String(raw.driverName || ''),
		remarks: String(raw.remarks || ''),
		items: safeItems,
		totalAmount: Number(totalAmount.toFixed(2)),
		totalQuantity,
		amountInWords: String(raw.amountInWords || ''),
	};
}

export function mapChallanFromDoc(docSnap) {
	return { id: docSnap.id, ...docSnap.data() };
}

async function logActivity(dcId, action, details) {
	try {
		await addDoc(collection(db, DC_ACTIVITY_COLLECTION), { dcId, action, details: details || {}, timestamp: serverTimestamp() });
	} catch (_) {}
}

export async function getChallansOrdered() {
	const q = query(collection(db, DC_COLLECTION), orderBy('createdAt', 'desc'));
	const snap = await getDocs(q);
	return snap.docs.map(mapChallanFromDoc);
}

export async function getChallanById(id) {
	const snap = await getDoc(doc(db, DC_COLLECTION, id));
	if (!snap.exists()) return null;
	return mapChallanFromDoc(snap);
}

export async function createChallan(raw) {
	const data = normalizeChallanForSave(raw);
	const now = serverTimestamp();
	const ref = await addDoc(collection(db, DC_COLLECTION), { ...data, createdAt: now, updatedAt: now });
	await logActivity(ref.id, 'create', { vendorId: data.vendorId, totalAmount: data.totalAmount });
	return { id: ref.id, ...data };
}

export async function updateChallan(id, raw) {
	const data = normalizeChallanForSave(raw);
	await updateDoc(doc(db, DC_COLLECTION, id), { ...data, updatedAt: serverTimestamp() });
	await logActivity(id, 'update');
}

export async function deleteChallan(id) {
	await deleteDoc(doc(db, DC_COLLECTION, id));
	await logActivity(id, 'delete');
}

export async function bulkDeleteChallans(ids) {
	const batch = writeBatch(db);
	ids.forEach((id) => batch.delete(doc(db, DC_COLLECTION, id)));
	await batch.commit();
	for (const id of ids) logActivity(id, 'bulk_delete');
}
